<template>
  <div id="app">
    <router-view/>
    <mt-popup class='showImg' :modal=false v-model="showImg" position="right">
      
      <mt-swipe class="homePageswipe" :auto="0" :continuous='false' :show-indicators="false">
        <mt-swipe-item v-for='(item,$index) in datasrc' :key='$index' v-if='item'>
          <div :style="{backgroundImage:'url('+item+')'}" @click="remove" ></div>
        </mt-swipe-item>
      </mt-swipe>
    </mt-popup>
  </div>
</template>

<script>
  import { mapActions } from 'vuex';
  import Analyse from '@/utils/analyse'
  export default {
    name: 'App',
    data(){
      return {
        showImg:false,
				datasrc:''
      }
    },
    methods:{
      ...mapActions(['GET_USER_INFO']),
      remove(){
        event.stopPropagation();
        this.showImg = false;
      }
    },
    created() {
      this.$bus.$on('showImg', (data) => { 
        this.datasrc = [...data];
        this.showImg = true;
      });
      this.GET_USER_INFO().then(()=>{
        Analyse.setCustomVar('用户类型', this.$store.state.config.UserType.find(({value})=>value===this.$store.state.userInfo.userType).label)
        console.log('config.UserType=', this.$store.state.config.UserType)
        console.log('userInfo.userType=', this.$store.state.userInfo.userType)
        console.log('用户类型=', this.$store.state.config.UserType.find(({value})=>value===this.$store.state.userInfo.userType).label)
        console.log('userId=', this.$store.state.userInfo.userId)
      })
    },
  }
</script>

<style lang="scss">

  html {
    background-color: #f2f2f2;
  }

  body {
    font-size: 16px;
    color: #333;
  }
  a{
    color: #26a2ff;
  }

  #app {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    overflow-x: hidden;
  }
  .showImg,.homePageswipe{
      width: 100%;
      height:100%;
      background:rgba(0,0,0,1);
      div{
        position: absolute;
        top: 0;
        left: 0;
        right:0;
        bottom:0;
        margin:auto;
        background-size: contain;
        background-position:center;
        background-repeat: no-repeat;
      }
    }
  .fade-enter-active,
  .fade-leave-active {
    // transform: translate3d(0, 0, 0);
    // transition: transform 0.5s, opacity 0.5s;
  }

  .fade-enter,
  .fade-leave-to {
    // transform: translate3d(100%, 0, 0);
    // transition: transform 0.5s, opacity 0.5s;
  }
</style>



