import axios from 'axios'
import Analyse from '@/utils/analyse'
import router from '@/router'
import store from '@/store'
import port from '@/assets/js/port'
import {actionType} from '@/assets/js/actionType'

// 公众号简介
const MPCONFIG = {
  title:'號角文化',
  desc:'致力于传播海洋文化，为热爱海洋文化的朋友提供分享、沟通、互动的平台，为企业和目标群体搭建关于海洋文化交流的桥梁，提供更多更全面的海洋知识与资源给有需要的人士'
}
class ConfigWechat{
  configPromise = null
  /**
   * config
   * @description 配置微信sdk
   * @return {promise}
   */
  config=()=>{
    console.log('store.state.wx_config.appId====', store.state.wx_config.appId)
    if(!this.configPromise){
      // 获取微信配置
      this.configPromise = axios.get(port.GET_WX_CONFIG, {
          params: {
            url: decodeURIComponent(location.href)
          }
        }).then(({code, data, msg}) => {
          if (code === 200) {
            console.log('data=', data)
            store.commit(actionType.set_wx_config, data)
            return new Promise(res=>{
              wx.config({
                debug: false, // 开启调试模式,调用的所有api的返回值会在客户端alert出来，若要查看传入的参数，可以在pc端打开，参数信息会通过log打出，仅在pc端时才会打印。
                appId: store.state.wx_config.appId, // 必填，公众号的唯一标识
                timestamp: store.state.wx_config.timestamp, // 必填，生成签名的时间戳
                nonceStr: store.state.wx_config.nonceStr, // 必填，生成签名的随机串
                signature: store.state.wx_config.signature,// 必填，签名
                jsApiList: ['onMenuShareTimeline', 'onMenuShareAppMessage', 'onMenuShareQQ', 'onMenuShareWeibo', 'onMenuShareQZone'] // 必填，需要使用的JS接口列表
              })
              wx.ready(()=> {   //需在用户可能点击分享按钮前就先调用
                this.updateShareData()
                res()
              });
          })
        }
      })
    }
    return this.configPromise    
  }
  /**
   * updateShareData
   * @description 更新分享信息
   * @param {string} title 分享标题
   * @param {string} desc 分享描述
   * TODO: 默认分享描述应该是號角文化的简介，而不是title
   * @param {string} link 分享链接，该链接域名或路径必须与当前页面对应的公众号JS安全域名一致
   * @param {string} imgUrl 分享图标
   * @return {promise}
   */
  updateShareData=(title=MPCONFIG.title, desc=MPCONFIG.desc, link=location.href, imgUrl=`${location.origin}/hornCultureProject/dist/static/img/share.jpg`, type='link')=>{
    return this.config().then(()=>{
      return new Promise(res=>{
        wx.onMenuShareAppMessage({
          title, // 分享标题
          desc, // 分享描述
          link, // 分享链接，该链接域名或路径必须与当前页面对应的公众号JS安全域名一致
          imgUrl, // 分享图标
          type, // 分享类型,music、video或link，不填默认为link
          success:()=>{
            Analyse.onMenuShareAppMessage(router.match(link).hash,title)
            //这里是回调函数
            console.log('share success')
            res()
          }
        })
        wx.onMenuShareTimeline({
          title, // 分享标题
          desc, // 分享描述
          link, // 分享链接，该链接域名或路径必须与当前页面对应的公众号JS安全域名一致
          imgUrl, // 分享图标
          type, // 分享类型,music、video或link，不填默认为link
          success:()=>{
            Analyse.onMenuShareTimeline(router.match(link).hash,title)
            //这里是回调函数
            console.log('share success')
            res()
          }
        })
      })
    })
  }
  
}
export default new ConfigWechat()


