import axios from 'axios'
import port from '@/assets/js/port'
import ImageCompressor from 'image-compressor.js'
import { Indicator, MessageBox } from 'mint-ui'

let compressorImage = file => {
  return new Promise((resolve, reject) => {
    Indicator.open('图片压缩中')
    new ImageCompressor(file, {
      quality: 0.8,
      maxWidth: 750,
      convertSize: 2000000,
      success: result => {
        Indicator.close()
        resolve(result)
      },
      error: err => {
        MessageBox.alert('图片压缩失败')
        reject(err)
      },
    })
  })
}

let uploadImage = file => {
  return new Promise((resolve, reject) => {
    compressorImage(file).then(res => {
      Indicator.open('图片上传中')
      let formData = new FormData()
      formData.append('imgType', 1)
      formData.append('upload', res, res.name)
      axios.post(port.UPLOAD_IMG, formData).then(res => {
        Indicator.close()
        if (!res.code === 200) {
          MessageBox.alert('图片上传失败')
        }
        resolve(res)
      }).catch(err => {
        MessageBox.alert('图片上传失败')
        reject(err)
      })
    })
  })
}
export default {
  compressorImage,
  uploadImage,
}



