import store from '@/store'
class Analyse{
  constructor() {
    // this.setAutoPageview(false)
  }
  /**
   * trackevent
   * @description 统计自定义事件
   * @param {string} category：[request|response]事件类别，必填项，表示事件发生在谁身上，如“视频”、“小说”、“轮显层”等等。
   * @param {string} action：事件操作，必填项，表示访客跟元素交互的行为动作，如"播放"、"收藏"、"翻层"等等。
   * @param {string} label：事件标签，选填项，用于更详细的描述事件，从各个方面都可以，比如具体是哪个视频，哪部小说，翻到了第几层等等。
   * @param {string} value：事件值，选填项，整数型，用于填写打分型事件的分值，加载时间型事件的时长，订单型事件的价格等等。
   * @param {string} nodeid：div元素id，选填项，填写网页中的div元素id值，用于在“用户视点”功能上重绘元素的事件发生情况。
   */
  trackevent=(category='',action='',label='',value='',nodeid)=>{
    _czc.push(['_trackEvent',category,action,label,value,nodeid]);
  }

  /**
   * pageView
   * @description 统计页面浏览
   * @param {string} content_url：为需要统计PV的页面、弹层指定URL地址。
   * @param {string} referer_url：该受访页面的来源页URL地址。
   */
  pageView=(content_url,referer_url)=>{
    _czc.push([ '_trackPageview',content_url,referer_url]);
  }

  /**
   * setCustomVar
   * @description 自定义访客种类
   * @param {string} name：自定义访客种类，必填项，表示观察访客的角度，如“会员级别”、“访客来源”等。每个访客（cookie）最多可拥有5个不同的name。
   * @param {string} value：自定义访客值，必填项，如“VIP1”、“VIP2”、“金账户”等（对应name为“访客级别”的情况）。
   * @param {string} time：有效时长，数值项，选填，表示本自定义访客值的有效时间，以“秒”为单位。不填或“1”表示长期有效，“0”表示仅发包页面有效，“2”表示仅本次访问之中有效。
   */
  setCustomVar=(name,value,time)=>{
    _czc.push([ '_setCustomVar',name,value,time]);
  }

  /**
   * setAutoPageview
   * @description 是否自动发送页面PV的统计请求
   * @param {boolean} autopageview 关闭自动发送，填false ;开启自动发送，为true，不调用时默认为true
   */
  setAutoPageview=(autopageview)=>{
    _czc.push(["_setAutoPageview",autopageview]);
  }
  /**
   * responseError
   * @description 请求答复异常
   * @param {string} url 
   * @param {code} url 业务状态码
   * @param {msg} url 答复信息
   */
  responseError=(url='', code='', msg='')=>{
    this.trackevent('response', url, code, msg)
  }
  /**
   * onMenuShareAppMessage
   * @description 分享好友
   * @param {string} url 
   * @param {string} title 分享标题
   */
  onMenuShareAppMessage=(url='', title='')=>{
    const userType = store.state.config.UserType.find(({value})=>value===store.state.userInfo.userType).label || '其他'
    this.trackevent('shareAppMessage', url, userType, title)
  }
  /**
   * onMenuShareTimeline
   * @description 分享朋友圈
   * @param {string} url 
   * @param {string} title 分享标题
   */
  onMenuShareTimeline=(url='', title='')=>{
    const userType = store.state.config.UserType.find(({value})=>value===store.state.userInfo.userType).label || '其他'
    this.trackevent('shareTimeline', url, userType, title)
  }
}
export default new Analyse()


