import Vue from 'vue'
import Vuex from 'vuex'
import Axios from 'axios'
import {actionType} from '@/assets/js/actionType'
import config from './modules/config'
import write from './modules/write'
import userInfo from './modules/userInfo'

Vue.use(Vuex)

const state = {
  token: localStorage.getItem('token'),
  code: localStorage.getItem('wx_code'),
  wx_config: {
    appId: '', // 必填，公众号的唯一标识
    timestamp: '', // 必填，生成签名的时间戳
    nonceStr: '', // 必填，生成签名的随机串
    signature: '',// 必填，签名
  },
  detail: {
    title: '',
    id: 0,
  }
}
const getters = {}
const mutations = {
  [actionType.set_wx_code](state, payload) {
    state.code = payload
    localStorage.setItem('wx_code', state.code)
  },
  [actionType.set_token](state, payload) {
    state.token = payload
    localStorage.setItem('token', state.token)
  },
  [actionType.set_wx_config](state, payload) {
    state.wx_config.appId = payload.appid
    state.wx_config.timestamp = payload.timestamp
    state.wx_config.nonceStr = payload.noncestr
    state.wx_config.signature = payload.signature
  },
  [actionType.set_detail](state, payload) {
    state.detail.title = payload.title
    state.detail.id = payload.id
  },
}
const actions = {}

export default new Vuex.Store({
  state,
  getters,
  actions,
  mutations,
  modules: {
    config,
    userInfo,
    write
  }
})



